import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { environment } from "src/environments/environment";
import { Plano } from "../model/plano.model";

@Injectable()
export class PlanoController {
    private api: string = environment.api

    constructor(private http: HttpClient) {}

    listAll(): Promise<Plano[]> {
        return this.http.get<Plano[]>(`${this.api}/planos`)
            .toPromise()
            .then(data => data)
    }

    getPlano(cdPlano: number): Promise<Plano> {
        return this.http.get<Plano>(`${this.api}/planos/${cdPlano}`)
            .toPromise()
            .then(data => data)
    }

    createPlano(plano: Plano): Promise<any> {
        return this.http.post(`${this.api}/planos`, plano)
            .toPromise()
            .then(response => response)
    }

    updatePlano(plano: Plano): Promise<any> {
        return this.http.put(`${this.api}/planos/${plano.plano_id}`, plano)
            .toPromise()
            .then(response => response)

    }
}