import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { environment } from "src/environments/environment";
import { Cliente } from "src/app/model/cliente.model";

@Injectable()
export class ClienteController {
    private api: string = environment.api

    constructor(private _http: HttpClient) {}

    listAllByUsuario(usuario_id: number) {
        return this._http
            .get(`${this.api}/cliente/${usuario_id}`)
            .toPromise()
            .then(data => {
                return data
            })
    }

    async getByUsuarioCliente(usuario_id: number, cliente_id: number): Promise<Cliente> {
        return await this._http
            .get<Cliente>(`${this.api}/cliente/${usuario_id}/${cliente_id}`)
            .toPromise()
            .then(data => {
                return data
            })
    }

    createCliente(cliente: Cliente): Promise<any> {
        return this._http
            .post(`${this.api}/cliente`, cliente, {responseType: 'text'})
            .toPromise()
            .then(data => {
                return data
            })
    }

    updateCliente(cliente: Cliente): Promise<any> {
        return this._http
            .put(`${this.api}/cliente`, cliente, {responseType: 'text'})
            .toPromise()
            .then(data => {
                return data
            })
    }
}
