import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { PlanoController } from 'src/app/controllers/plano.controller';
import { ToastService } from 'src/app/shared/components/toast.service';

@Component({
  selector: 'app-plans',
  templateUrl: './plans.component.html',
  styleUrls: ['./plans.component.css']
})
export class PlansComponent implements OnInit {

  planos: any[] = []

  constructor(
    private controller: PlanoController,
    private toast: ToastService,
    private router: Router,
    private route: ActivatedRoute
    ) { }

  ngOnInit(): void {
    this.populateTable()
  }

  async populateTable() {
    try {
      this.planos = await this.controller.listAll()
    } catch(error) {
      this.toast.present(error.error.message)
    }
  }

  public goTo(id: number) {
    this.router.navigate([id], {relativeTo: this.route})
  }

}
