import { AfterViewInit, Component, OnInit } from '@angular/core';
import { UsuarioController } from 'src/app/controllers/usuario.controller';
import { Usuario } from 'src/app/model/usuario.model';
import { ToastService } from 'src/app/shared/components/toast.service';
import { categorias } from 'src/app/model/enums/categoria.enum'
import { status } from 'src/app/model/enums/status.enum'

declare var $;

@Component({
    selector: 'app-users',
    templateUrl: './users.component.html',
})
export class UsersComponent implements OnInit, AfterViewInit {

    usuarios: any[] = []

    constructor(
        private usuarioController: UsuarioController,
        private toast: ToastService
        ) {}

    ngOnInit(): void {}

    ngAfterViewInit() {
        this.populateTable()
    }

    async populateTable() {
        try {
            let usuarios = await this.usuarioController.listAllAdmin()
            this.usuarios = usuarios
        } catch(error) {
            this.toast.present(error.error.message)
        }
    }

    public getCategoria(cdCategoria): string {
        return categorias[cdCategoria]
    }

    public getStatus(cdStatus): string {
        return status[cdStatus]
    }
}
