import { Usuario } from 'src/app/model/usuario.model';
import { FormControl, FormGroup } from '@angular/forms';
import { Component, OnInit } from '@angular/core';
import { UsuarioController } from 'src/app/controllers/usuario.controller';
import { ToastService } from 'src/app/shared/components/toast.service';
import * as moment from 'moment'
import { ActivatedRoute, Router } from '@angular/router';

@Component({
    selector: 'app-create',
    templateUrl: './create.component.html',
})
export class CreateComponent implements OnInit {
    public formGroup: FormGroup = new FormGroup({})
    public formPass: FormGroup = new FormGroup({})

    public curDate: string = moment(new Date()).format('DD/MM/yyyy hh:mm:ss')
    public id
    public passData = {
        pass: '',
        confirm: ''
    }

    constructor(
        private controller: UsuarioController,
        private toast: ToastService,
        private route: ActivatedRoute,
        private router: Router
    ) {}

    ngOnInit(): void {
        Object.keys(new Usuario()).forEach(attr => {
            this.formGroup.addControl(attr, new FormControl())
        })

        this.formPass.addControl('pass', new FormControl())
        this.formPass.addControl('confirm', new FormControl())

        this.id = this.route.snapshot.paramMap.get('id')
        if(this.id)
            this.populateFG(this.id)
    }

    public async salvar() {
        let usuario = this.formGroup.value

        try {
            if(usuario && this.id)
                await this.controller.updateUsuarioAdmin(this.id, <Usuario> usuario)
            else
                this.createUsuario(usuario, this.passData)

            this.toast.present('Usuário salvo com sucesso')
            this.router.navigate(['/users'])
        } catch(error) {
            this.toast.present('Erro no processo de salvamento de usuário')
        }

    }

    private async populateFG(id: string | number) {
        try {
            let usuario = await this.controller.getById(<number> id)
            Object.keys(this.formGroup.controls).forEach(control => {
                this.formGroup.get(control).setValue(usuario[control])
            })

            this.curDate = moment(usuario.usuario_data).format('DD/MM/yyyy hh:mm:ss')
        } catch(error) {
            console.error(error)
            this.toast.present('Erro ao recuperar usuário')
        }
    }

    private async createUsuario(usuario: Usuario, passData: any) {
        this.passData.pass = this.formPass.value.pass
        this.passData.confirm = this.formPass.value.confirm

        try {
            if(passData.pass){
                if(passData.pass === passData.confirm) {
                    usuario.usuario_senha = passData.pass
    
                    this.controller.createUsuario(usuario)
                } else {
                    this.toast.present('As duas senhas devem ser iguais')
                }
            } else {
                this.toast.present('É necessária uma senha para inserção de usuário')
            }
        
        } catch(error) {
            console.error(error)
        }
    }
}
