import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { PlanoController } from 'src/app/controllers/plano.controller';
import { Plano } from 'src/app/model/plano.model';
import { ToastService } from 'src/app/shared/components/toast.service';

@Component({
  selector: 'app-form',
  templateUrl: './form.component.html',
  styleUrls: ['./form.component.css']
})
export class FormComponent implements OnInit {

  title: string = 'Cadastro de Plano'
  formGroup: FormGroup = new FormGroup({})

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private controller: PlanoController,
    private toast: ToastService
  ) { }

  ngOnInit(): void {
    this.buildFormGroup()
    let id = this.route.snapshot.params['id']

    if (id)
      this.getPlano(id)
  }

  buildFormGroup() {
    Object.keys(new Plano()).forEach(attr => {
      this.formGroup.addControl(attr, new FormControl())
    })
  }

  async getPlano(cdPlano) {
    try {
      let plano = await this.controller.getPlano(cdPlano)

      Object.keys(this.formGroup.controls).forEach(control => {
        this.formGroup.get(control).setValue(plano[control])
      })

    } catch (error) {
      this.toast.present(error.error.message)
    }
  }

  async savePlano() {
    try {
      let plano = <Plano> this.formGroup.value

      if(plano.plano_id && plano.plano_id > 0)
        this.controller.updatePlano(plano)
      else
        this.controller.createPlano(plano)
        
      this.toast.present('Plano salvo com sucesso')
    } catch(error) {
      console.error(error)
      this.toast.present('Erro ao cadastrar plano')
    }
  }

}
