export class CardData {

    title: string
    icon: string
    color: string
    stat: string
    subtitle: string
    link: string

    constructor(title: string, icon: string, color: string, stat: string, subtitle?: string, link?: string) {
        this.title = title
        this.icon = icon
        this.color = color
        this.stat = stat
        this.subtitle = subtitle
        this.link = link || 'home'
    }

}