import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { UsuarioController } from 'src/app/controllers/usuario.controller';
import { Usuario } from 'src/app/model/usuario.model';
import { ToastService } from 'src/app/shared/components/toast.service';
import * as moment from 'moment'

@Component({
    selector: 'app-create',
    templateUrl: './create.component.html',
})
export class CreateComponent implements OnInit {

    private id: number
    public formGroup: FormGroup = new FormGroup({})
    public curDate: string = moment().format('DD/MM/yyyy hh:mm:ss')

    constructor(
        private route: ActivatedRoute,
        private toast: ToastService,
        private controller: UsuarioController,
        private router: Router
    ) {}

    ngOnInit(): void {
        this.createFG()
    }

    private createFG() {
        Object.keys(new Usuario()).forEach(attr => {
            this.formGroup.addControl(attr, new FormControl())
        })

        this.formGroup.get('usuario_vip').setValue(0)
        this.checkUser()
    }

    private checkUser() {
        let id = this.route.snapshot.paramMap.get('id')

        if(id) {
            this.id = Number(id)
            this.populateFG()
        }   
    }

    private async populateFG() {
        try {
            let usuario = await this.controller.getById(this.id)

            if(usuario) {
                Object.keys(this.formGroup.controls).forEach(attr => {
                    this.formGroup.get(attr).setValue(usuario[attr])
                })
            }

            this.curDate = moment(usuario.usuario_data).format('DD/MM/yyyy hh:mm:ss')
        } catch(error) {
            this.toast.present('Erro ao recuperar informações do usuário')
            console.error(error)
        }
    }

    public async save() {
        try {
            if(!this.id)
                this.insertUser()
            else
                this.udpateUser()

            this.router.navigate(['/clients'])

        } catch(error) {
            this.toast.present('Erro no processo de salvamento dos dados')
        }
    }

    private async insertUser() {
        try {
            let usuario = <Usuario> this.formGroup.value
            let response = await this.controller.createUsuario(usuario)

            console.log(response)
            this.toast.present('Usuário salvo com sucesso')
        } catch(error) {
            console.error(error)
        }
    }

    private async udpateUser() {
        try {
            let usuario = <Usuario> this.formGroup.value
            let response = await this.controller.updateUsuario(this.id, usuario)

            console.log(response)
            this.toast.present('Usuário salvo com sucesso')
        } catch(error) {
            console.error(error)
        }
    }
}
