import { Component, OnInit } from '@angular/core';
import { ClienteController } from 'src/app/controllers/cliente.controller';
import { PlanoController } from 'src/app/controllers/plano.controller';
import { UsuarioController } from 'src/app/controllers/usuario.controller';
import { CardData } from 'src/app/model/card.data.model'

const userIcon = 'fas fa-users'
const plansIcon = 'fas fa-dollar-sign'

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  usuarioStats = new CardData('Administradores', userIcon, 'danger', 'data', 'como administradores', '/users')
  clienteStats = new CardData('Usuários', userIcon, 'warning', 'data', 'como clientes', '/clients')
  planoStats = new CardData('Planos', plansIcon, 'success', 'data', 'disponíveis para o app', '/plans')

  constructor(
    private usuarioController: UsuarioController,
    private planoController: PlanoController,
    private clienteController: ClienteController
  ) { }

  ngOnInit(): void {
    this.getCardStats()
  }

  private async getCardStats() {

    this.usuarioStats.stat = this.setUserStat((await this.usuarioController.listAllAdmin()).length)
    this.clienteStats.stat = this.setUserStat((await this.usuarioController.listAll()).length)
    this.planoStats.stat = this.setPlanoStat((await this.planoController.listAll()).length)

  }

  private setUserStat(admins: number) {
    if(admins > 1)
      return admins + ' usuários cadastrados'
    
    if(admins == 1)
      return admins + ' usuário cadastrado'

    return 'Nenhum usuário cadastrado'
  }

  private setPlanoStat(planos: number) {
    if(planos > 1)
      return planos + ' planos cadastrados'

    if(planos == 1)
      return planos + ' plano cadastrado'

    return 'Nenhum plano cadastrado'
  }

}
