import { HttpClient } from '@angular/common/http'
import { Injectable } from '@angular/core'
import { environment } from 'src/environments/environment'
import { Usuario } from '../model/usuario.model'

@Injectable()
export class UsuarioController {
    private api: string = environment.api

    constructor(private http: HttpClient) {}

    listAll(): Promise<Usuario[]> {
        return this.http
            .get<Usuario[]>(`${this.api}/usuario`)
            .toPromise()
            .then((data) => {
                return data
            })
    }

    listAllAdmin(): Promise<Usuario[]> {
        return this.http
            .get<Usuario[]>(`${this.api}/usuario/admin`)
            .toPromise()
            .then((data) => {
                return data
            })
    }

    getById(usuario_id: number): Promise<Usuario> {
        return this.http
            .get<Usuario>(`${this.api}/usuario/${usuario_id}`)
            .toPromise()
            .then((data) => {
                return data
            })
    }

    createUsuario(usuario: Usuario): Promise<any> {
        return this.http.post(`${this.api}/usuario`, usuario)
            .toPromise()
            .then(response => response)
    }

    updateUsuario(usuario_id, usuario: Usuario): Promise<any> {
        return this.http.put(`${this.api}/usuario/${usuario_id}`, usuario)
        .toPromise()
        .then(data => {
            return data
        })
    }

    createUsuarioAdmin(usuario: Usuario): Promise<any> {
        return this.http.post(`${this.api}/usuario/admin`, usuario)
        .toPromise()
        .then(data => {
            return data
        })
    }

    updateUsuarioAdmin(usuario_id, usuario: Usuario): Promise<any> {
        return this.http.put(`${this.api}/usuario/admin/${usuario_id}`, usuario)
        .toPromise()
        .then(data => {
            return data
        })
    }
}
